import { defHttp } from '/@/utils/http/axios';
import type { SelectProps } from 'ant-design-vue';
import { ref } from 'vue';

enum Api {
  //顶端指标
  getTopIndexUrl = '/da/elec-page/index',
  //电站能效
  getEnergyUrl = '/da/elec-page/energy/ranking',
  //电量排行
  getPowerUrl = '/da/elec-page/power/ranking',

  //损失分布
  getLossTypeUrl = '/da/elec-page/loss/type',
  //损失排行
  getLossRankingUrl = '/da/elec-page/loss/ranking',
  //电量趋势
  getPowerTrendUrl = '/da/elec-page/trend',

  //计划完成
  getPlannedPowerUrl = '/da/elec-page/planned/ranking',
  //厂用电
  getFactroyUsedPowerUrl = '/da/elec-page/factory/used',
}
export const getTopIndexData = (params) => defHttp.post({ url: Api.getTopIndexUrl, params }, {});
export const getEnergyData = (params) => defHttp.post({ url: Api.getEnergyUrl, params }, {});
export const getPowerData = (params) => defHttp.post({ url: Api.getPowerUrl, params }, {});

export const getLossTypePieData = (params) => defHttp.post({ url: Api.getLossTypeUrl, params }, {});
export const getLossRankingData = (params) =>
  defHttp.post({ url: Api.getLossRankingUrl, params }, {});
export const getPowerTrendData = (params) =>
  defHttp.post({ url: Api.getPowerTrendUrl, params }, {});

export const getPlannedPowerData = (params) =>
  defHttp.post({ url: Api.getPlannedPowerUrl, params }, {});
export const getFactroyUsedPowerData = (params) =>
  defHttp.post({ url: Api.getFactroyUsedPowerUrl, params }, {});

